import React, { Component } from 'react'
import { Button, Modal, message } from 'antd'
import PropTypes from 'prop-types'
import { getTables } from '@/services/table.js'


class TablePage extends Component {
    state = {
        showClone: false,
        total: 0
    }
    componentDidMount() {
        message.info('antd')
        getTables({
            owner: window.localStorage.realname
        }).then(res => {
            this.setState({
                total: res.total
            })
        })
    }

    render() {
        return (
            <>
                <div style={{backgroundColor:'red'}}>
                    <Button onClick={() => this.props.history.push('/table-search')}></Button>
                    <Button type='primary' onClick={() => this.props.history.push('/create-table')}></Button>
                    <Button type='primary' onClick={() => this.setState({ showClone: true })}></Button>
                    <Button type='primary' onClick={() => this.props.history.push('/auth-manage')} disabled={!this.state.total}></Button>
                </div>
                <p>fdfasdfsa</p>
                <Button type='primary'>primary</Button>
                <p>fdsafsdggfkdgjdsllgdjlgj</p>
                <p>fdsf3223</p>
            </>
        )
    }
}

TablePage.propTypes = {
    history: PropTypes.object // React-Router API
}

export default TablePage
